import { Component ,Input , OnInit, ElementRef, ViewChild } from '@angular/core';

@Component({
  selector: 'components-item-svg',
  templateUrl: 'components-item-svg.html'
})
export class ComponentsItemSvgComponent implements OnInit {
  
  @Input() itemSvg: string; 
  @ViewChild('svgContainer') dataContainer: ElementRef;

  loadData(data) {
        this.dataContainer.nativeElement.innerHTML = data;
      }

  text: string;

  constructor() {
    console.log('Hello ComponentsItemSvgComponent Component');
    this.text = 'Hello World';
  }

  ngOnInit()  {
    this.loadData(this.itemSvg)
  }

}



