import { Component, Input, OnInit, ViewChild, ElementRef, Pipe, Sanitizer } from '@angular/core';
import { Language } from '../../../src/models/language/language'




@Component({
  selector: 'radar-chart',
  templateUrl: 'radar-chart.html'
})
export class RadarChartComponent implements OnInit{
  
  @Input() data: Language[]; 

//   @ViewChild('svgContainer') dataContainer: ElementRef;

//   loadData(data) {
//    this.dataContainer.nativeElement.innerHTML = data;
//  }   
 
  labels:string[] = [];
  labelsData:number[] =[];
  labelsSvg:string[]=[];
  
  constructor() {
  
  }

  ngOnInit(){
  
    for (let item = 0; item < this.data.length; item++) {
      
      this.labels.push(this.data[item].name);
      this.labelsData.push(this.data[item].value);
      this.labelsSvg.push(this.data[item].svg)
    }
    console.log(this.labelsSvg)
    //this.loadData(this.labelsSvg)  
  }

  // Radar
  
  public radarChartLabels:string[] = this.labels;
  
  public radarChartData:any = [
    {data: this.labelsData}
  ];

  public radarChartType:string = 'radar';
  
  public options: any = {
    legend: { display: false },
    responsive: true,
    scale : {
       ticks: {
          beginAtZero:true,
          stepValue : 2,
          max : 10,
        }
     
    }
    
  }

  public chartColors: Array<any> = [
    { // first color
      backgroundColor: 'rgba(0,0,255,0.3)',
      borderColor: 'rgba(0,0,255,0.8)',
      pointBackgroundColor: 'rgba(0,0,255,0.4)',
      pointBorderColor: 'rgba(0,0,255,0.8)',
      pointBorderWidth: 4,
      pointHoverBackgroundColor: '#fff',
      pointHoverBorderColor: 'rgba(0,0,255,1)'
    }
  ]

  // events
  public chartClicked(e:any):void {
    console.log(e);
  }
 
  public chartHovered(e:any):void {
    console.log(e);
  }

}



