import { Component } from '@angular/core';

import { AboutPage } from '../about/about';
import { ContactPage } from '../contact/contact';
import { HomePage } from '../home/home';

class Page {
    page: any;
    title: String;
    icon: String;

    constructor( page: any, title: String, icon: String ){
      this.page=page;
      this.title= title;
      this.icon= icon;
    }

}

@Component({
  templateUrl: 'tabs.html'
})


export class TabsPage {

  tab1Root = new Page(HomePage,"Home","home");
  tab2Root = new Page(AboutPage,"About","information-circle");
  tab3Root = new Page(ContactPage,"Contact","contacts");

  tabslist = [this.tab1Root,this.tab2Root,this.tab3Root];

  constructor() {

  }
}



